html[dir="rtl"] body {
  text-align: right; }
  html[dir="rtl"] body * {
    letter-spacing: 0; }

.loginWrap .contentBox h1 {
  line-height: 1.3; }

.apItem .statusBox > div:before {
  right: 0;
  left: auto; }

html[dir="rtl"] .daItem .textBox p {
  line-height: 1.5; }

.eduItem .tagBox {
  left: 0;
  right: auto;
  border-radius: 0.8rem 0 1rem 0; }

.eduItem .badgeBox {
  right: 1rem;
  left: auto; }

.eduWoItem .title_ {
  line-height: 1.2; }

.eduWoItem .dateBox {
  right: 1rem;
  left: auto; }

html[dir="rtl"] .eduWoItem .textBox p {
  line-height: 1.7; }

.pageBanner h1 {
  line-height: 1.3; }

html[dir="rtl"] body .fullCalendar .fc-daygrid-day-frame {
  direction: rtl; }

html[dir="rtl"] body .fullCalendar .fc-daygrid-day-events .fc-event {
  text-align: right; }
  html[dir="rtl"] body .fullCalendar .fc-daygrid-day-events .fc-event::before {
    right: 0.3rem;
    left: auto; }

html[dir="rtl"] body .fullCalendar .fc-daygrid-day-frame:after {
  right: 0.8rem;
  left: auto; }
  @media only screen and (max-width: 767px) {
    html[dir="rtl"] body .fullCalendar .fc-daygrid-day-frame:after {
      right: 0.2rem; } }

.appointmentDetails .appNo {
  right: 1.5rem;
  left: auto; }
  @media only screen and (max-width: 767px) {
    .appointmentDetails .appNo {
      right: 0; } }

.tabWrap .link_ {
  line-height: 1.5;
  padding-bottom: 0.4rem; }

.finalQuizTab.v2 .fqItem::before {
  transform-origin: left; }

.finalQuizTab.v2 .fqItem.acShow::before {
  transform-origin: right; }

@media only screen and (min-width: 981px) {
  .finalQuizTab.v2 .fqItem:nth-child(odd) {
    border-left: 1px solid rgba(112, 112, 112, 0.1);
    border-right: none; } }

.finalQuizTab.v2:before {
  left: auto;
  right: calc(25% * (var(--activeTab) - 1)); }
  @media only screen and (max-width: 980px) {
    .finalQuizTab.v2:before {
      right: 0;
      top: calc(25% * (var(--activeTab) - 1)); } }

.finalQuizTab:before {
  right: 0;
  left: auto; }

.pageWrap .svgBox {
  left: auto;
  right: -15rem;
  animation: rotating 180s linear infinite reverse; }

.crumbList .crumbItem::before {
  border-right: 12px solid #ab8240;
  border-left: none; }

.linkBox .link_.active::before {
  transform-origin: right; }

.linkBox .link_:hover::before {
  transform-origin: right; }

.form-v3 .modal-overlay {
  left: auto;
  right: 50%;
  transform: translateX(50%); }

.tableWrap .input-field .wIcon .iconBox {
  right: 0 !important;
  left: auto; }

.fancybox-slide--html .fancybox-close-small {
  right: auto;
  left: 0; }
